
import { defineComponent, ref } from "vue";

import { useRouter } from "vue-router";

import axios from "axios";

import { Toast } from "vant";

export default defineComponent({
  setup() {
    let router: any = useRouter();

    let isPassword: any = ref<boolean>(true);

    let userInfo: any = ref<any>({
      phone: "",
      password: "",
      email: "",
      validCode: "",
    });

    let text: any = ref<string>("发送验证码");

    let isSend: any = ref<boolean>(false);

    //查看首页
    let goPage: (name: string) => void = (name: string): void => {
      router.push({ name });
    };

    //查看密码
    let viewPassword: () => void = (): void => {
      isPassword.value = !isPassword.value;
    };

    //发送验证码
    let sendValidCode: () => void = (): void => {

      let reg: RegExp = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;

      if (!reg.test(userInfo.value.email)) {
        Toast('邮箱格式不正确');
        return;
      }

      let time: number = 120;
      text.value = time + "s后重新发送";
      isSend.value = true;

      let timer: number = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          isSend.value = false;
          text.value = "发送验证码";
        } else {
          time--;
          text.value = time + "s后重新发送";
        }
      }, 1000);

      //发起获取邮箱验证码请求
      axios({
        method: "POST",
        url: "/emailValidCode",
        data: {
          email: userInfo.value.email,
        },
      })
        .then((result: any): void => {
          Toast(result.data.msg);
        })
        .catch((err: any): void => {
          
        });
    };

    //提交
    let commit: () => void = (): void => {
      //发起忘记密码请求

      axios({
        method: "POST",
        url: "/checkValidCode",
        data: {
          validCode: userInfo.value.validCode,
        },
      })
        .then((result: any): void => {
          if (result.data.code == "K001") {
            //验证成功
            updatePassword();
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //修改密码
    let updatePassword: () => void = (): void => {
      axios({
        method: "POST",
        url: "/retrievePassword",
        data: {
          phone: userInfo.value.phone,
          password: userInfo.value.password,
        },
      })
        .then((result) => {
          Toast(result.data.msg);
          if (result.data.code == "L001") {
            setTimeout(() => {
              router.push({ name: "Login" });
            }, 1000);
          }
        })
        .catch((err: any): void => {
          
        });
    };

    return {
      isPassword,
      userInfo,
      text,
      isSend,
      goPage,
      viewPassword,
      sendValidCode,
      commit
    };
  },
});
